import React, { useState } from 'react';
import { View, Text, TextInput, TouchableOpacity, StyleSheet, SafeAreaView, Alert, Image, Pressable, Platform } from 'react-native';
import axios from 'axios'; // Usamos axios para las peticiones HTTP

const RegisterScreen = ({ navigation }) => {
  const [nombreCompleto, setNombreCompleto] = useState('');
  const [correo, setCorreo] = useState('');
  const [telefono, setTelefono] = useState('');
  const [ocupacion, setOcupacion] = useState('');
  const [errors, setErrors] = useState({
    nombreCompleto: '',
    correo: '',
    telefono: '',
    ocupacion: '',
  });

  // Función para mostrar alertas compatibles con web y móviles
  const showAlert = (title, message) => {
    if (Platform.OS === 'web') {
      window.alert(`${title}\n${message}`); // Usar window.alert en la web
    } else {
      Alert.alert(title, message); // Usar Alert en iOS y Android
    }
  };

  const validateForm = () => {
    let isValid = true;
    const newErrors = { nombreCompleto: '', correo: '', telefono: '', ocupacion: '' };

    if (!nombreCompleto.trim()) {
      newErrors.nombreCompleto = 'El nombre completo es obligatorio.';
      isValid = false;
    }

    if (!correo.trim()) {
      newErrors.correo = 'El correo es obligatorio.';
      isValid = false;
    } else if (!/\S+@\S+\.\S+/.test(correo)) {
      newErrors.correo = 'El correo no es válido.';
      isValid = false;
    }

    if (!telefono.trim()) {
      newErrors.telefono = 'El teléfono es obligatorio.';
      isValid = false;
    } else if (!/^\d{10}$/.test(telefono)) {
      newErrors.telefono = 'El teléfono debe tener 10 dígitos.';
      isValid = false;
    }

    if (!ocupacion.trim()) {
      newErrors.ocupacion = 'La ocupación es obligatoria.';
      isValid = false;
    }

    setErrors(newErrors);
    return isValid;
  };

  const generateRandomCode = () => {
    return Math.floor(100000 + Math.random() * 900000).toString(); // Genera un número de 6 dígitos
  };

  /*const sendEmailWithCode = async (email, code) => {
    try {
      const response = await axios.post('https://us-central1-clubtoros-c8a29.cloudfunctions.net/sendEmailFunction', {
        email,
        code,
      });

      console.log('Correo enviado:', response.data);
      return response.data;
    } catch (err) {
      console.error('Error al enviar el correo:', err);
      throw new Error('No se pudo enviar el correo.');
    }
  };*/

  const handleRegister = async () => {
    if (validateForm()) {
      try {
        //const codigoAcceso = generateRandomCode();

        // Datos del usuario para enviar al servidor
        const userData = {
          nombre_completo: nombreCompleto,
          correo,
          celular: telefono,
          ocupacion,
        };

        // Enviar los datos al servidor
        const response = await axios.post('https://sistema.clubtoros.com/api/registro', userData);

        if (response.status === 201) {
          // Enviar el código por correo
          //await sendEmailWithCode(correo, codigoAcceso);

          showAlert('Éxito', 'Usuario registrado correctamente.');
          navigation.navigate('Login'); // Redirigir al login después del registro
        } else {
          showAlert('Error', response.data.message || 'Error al registrar el usuario.');
        }
      } catch (err) {
        console.error('Error al registrar el usuario:', err);

        if (err.response && err.response.status === 422) {
          // Manejar errores de validación del servidor
          const errorDetails = err.response.data;
          showAlert('Error de Validación', errorDetails.message || JSON.stringify(errorDetails));
        } else {
          showAlert('Error', 'Error al registrar el usuario. Por favor, inténtalo de nuevo.');
        }
      }
    }
  };

  return (
    <SafeAreaView style={styles.container}>
      <View style={styles.rectangle}>
        <View style={styles.leftColumn}>
          <Image
            source={require('../assets/logo.jpg')}
            style={styles.image}
            resizeMode="contain"
          />
        </View>
        <View style={styles.rightColumn}>
          <Text style={styles.welcomeText}>Registro</Text>
          <TextInput
            style={styles.input}
            placeholder="Nombre Completo"
            placeholderTextColor="#999"
            value={nombreCompleto}
            onChangeText={setNombreCompleto}
          />
          {errors.nombreCompleto ? <Text style={styles.errorText}>{errors.nombreCompleto}</Text> : null}

          <TextInput
            style={styles.input}
            placeholder="Correo"
            placeholderTextColor="#999"
            value={correo}
            onChangeText={setCorreo}
            keyboardType="email-address"
          />
          {errors.correo ? <Text style={styles.errorText}>{errors.correo}</Text> : null}

          <TextInput
            style={styles.input}
            placeholder="Teléfono"
            placeholderTextColor="#999"
            value={telefono}
            onChangeText={setTelefono}
            keyboardType="phone-pad"
          />
          {errors.telefono ? <Text style={styles.errorText}>{errors.telefono}</Text> : null}

          <TextInput
            style={styles.input}
            placeholder="Ocupación"
            placeholderTextColor="#999"
            value={ocupacion}
            onChangeText={setOcupacion}
          />
          {errors.ocupacion ? <Text style={styles.errorText}>{errors.ocupacion}</Text> : null}

          <Pressable style={styles.loginButton} onPress={handleRegister}>
            <Text style={styles.loginButtonText}>Registrarse</Text>
          </Pressable>

          <TouchableOpacity onPress={() => navigation.navigate('Login')}>
            <Text style={styles.linkText}>¿Ya tienes una cuenta? Inicia Sesión</Text>
          </TouchableOpacity>
        </View>
      </View>
    </SafeAreaView>
  );
};

const styles = StyleSheet.create({
  container: {
    flex: 1,
    backgroundColor: '#FFF',
    justifyContent: 'center',
    alignItems: 'center',
  },
  rectangle: {
    flexDirection: 'row',
    width: '80%',
    height: 500,
    borderRadius: 10,
    overflow: 'hidden',
  },
  leftColumn: {
    flex: 1,
    backgroundColor: '#FBBE08',
    justifyContent: 'center',
    alignItems: 'center',
  },
  rightColumn: {
    flex: 1,
    padding: 20,
    justifyContent: 'center',
  },
  welcomeText: {
    fontFamily: 'MiFuente',
    fontSize: 40,
    color: '#000',
    textAlign: 'center',
    paddingBottom: 20,
  },
  input: {
    height: 40,
    borderColor: '#DDD',
    borderWidth: 1,
    borderRadius: 20,
    marginBottom: 5,
    paddingHorizontal: 10,
  },
  loginButton: {
    backgroundColor: '#FBBE08',
    padding: 10,
    borderRadius: 20,
    alignItems: 'center',
    marginBottom: 15,
  },
  loginButtonText: {
    color: '#FFF',
    fontWeight: 'bold',
  },
  linkText: {
    color: '#FBBE08',
    textAlign: 'center',
    marginBottom: 10,
  },
  errorText: {
    color: 'red',
    fontSize: 12,
    marginBottom: 10,
  },
  image: {
    width: '80%',
    height: '80%',
  },
});

export default RegisterScreen;